jpg\");\n}\n\n.hero__inner {\n  text-align: center;\n  color: #dddd;\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.hero__inner h1 {\n  color: #fff;\n  font-weight: 600;\n  font-size: 36px;\n  text-shadow: 1px 1px #141414;\n}\n\n.hero__inner p {\n  margin-top: 0.5em;\n}\n\n/* Main Content Styles */\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.content__label {\n  font-size: 32px;\n  font-weight: bold;\n  margin-top: 0.5em;\n  text-align: center;\n}\n\n.content {\n  margin: 32px;\n  text-align: left;\n}\n\n.post-item {\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.post-city {\n  position: absolute;\n  border-radius: 5px;\n  background-color: #135ea8a2;\n  min-width: 6em;\n  padding: 0.5em;\n  color: white;\n  font-weight: 500;\n  margin: 14px 0;\n  text-align: center;\n}\n\n.post-pics {\n  width: 100%;\n  height: 50%;\n}\n\n.post-item-content {\n  padding: 1.5em;\n  width: 100%;\n  margin: 0;\n}\n\n.post-item-rating {\n  font-size: 12px;\n  text-transform: uppercase;\n  color: #999;\n  font-weight: 500;\n}\n\n.list-rating-value {\n  color: red;\n  text-decoration: none;\n  font-size: 2em;\n  padding: 10px;\n  min-width: 44px;\n  display: inline-block;\n}\n\n.post-name {\n  font-weight: 500;\n  font-size: 16px;\n  margin-top: 10px;\n  transition: 0.3s;\n  width: 100%;\n  align-items: center;\n}\n\n.post-name:hover {\n  opacity: 0.5;\n}\n\n.post-name a {\n  text-decoration: none;\n  color: black;\n  font-size: 1.5rem;\n  padding: 10px;\n}\n\n.post-desc {\n  margin-top: 10px;\n  font-size: 12px;\n  line-height: 1.5em;\n}\n\n/* Footer Styles */\nfooter {\n  background-color: #135ea8;\n  color: white;\n  font-size: 14px;\n  padding: 2.5em;\n  width: 100%;\n  text-align: center;\n}\n\n/* Restaurant Card Styles */\n.restaurant-card {\n  display: block;\n  width: 250px;\n  margin: 10px;\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  overflow: hidden;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.2s ease-in-out;\n  cursor: pointer;\n  text-decoration: none;\n}\n\n.restaurant-card:hover {\n  transform: scale(1.05);\n}\n\n.restaurant-image {\n  width: 100%;\n  height: 150px;\n  object-fit: cover;\n  border-radius: 8px 8px 0 0;\n}\n\n.restaurant-info {\n  padding: 16px;\n}\n\n.restaurant-title {\n  font-size: 1.2em;\n  margin-bottom: 8px;\n  color: #333;\n}\n\n.restaurant-description {\n  color: #555;\n}\n\n.restaurant-city {\n  font-style: italic;\n  color: #888;\n  margin-bottom: 8px;\n}\n\n/* Restaurant List Styles */\n.list-resto {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: start;\n  align-items: start;\n  padding: 20px;\n  gap: 20px;\n  \n  \n}\n\n/* Main Restaurant Container Styles */\n.main-resto_container {\n  padding: 20px;\n}\n\n.main-resto_label {\n  font-size: 1.5em;\n  margin-bottom: 20px;\n}\n\n/* Form Container Styles */\n.form-review {\n  margin: 20px;\n}\n\n/* Form Styles */\n.review-form {\n  max-width: 400px;\n  margin: auto;\n}\n\n.form-label {\n  font-size: 1.2em;\n  margin-bottom: 5px;\n  display: block;\n}\n\n.form-control {\n  width: 100%;\n  padding: 8px;\n  margin-bottom: 15px;\n  box-sizing: border-box;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  font-size: 1em;\n}\n\ntextarea.form-control {\n  resize: vertical;\n}\n\n/* Submit Button Styles */\n.btn {\n  background-color: #4caf50;\n  color: white;\n  padding: 10px 15px;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n}\n\n.btn:hover {\n  background-color: #45a049;\n}\n\n/* Optional: Add a primary button style */\n.btn-primary {\n  background-color: #007bff;\n}\n\n.btn-primary:hover {\n  background-color: #0056b3;\n}\n\n/* Detail Container Styles */\n.detail {\n  margin: 20px;\n  padding: 20px;\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  overflow: hidden;\n}\n\n/* Detail Info Container Styles */\n.container-info {\n  display: flex;\n}\n\n/* Detail Image Container Styles */\n.img-container {\n  margin-right: 20px;\n}\n\n/* Detail Info Styles */\n.detail-info {\n  list-style: none;\n  padding: 0;\n}\n\n.detail-info li {\n  margin-bottom: 10px;\n}\n\n/* Resto Name, Address, and Rating Styles */\n.resto-name, .resto-address, .resto-rating {\n  display: flex;\n  align-items: center;\n}\n\n.resto-name i, .resto-address i, .resto-rating i {\n  margin-right: 10px;\n}\n\n/* Menu List Styles */\n.restaurant-detail__menu-list {\n  display: flex;\n}\n\n/* Foods and Drinks Styles */\n.foods, .drinks {\n  margin-right: 20px;\n}\n\n.restaurant-detail__foods, .restaurant-detail__drinks {\n  list-style: none;\n  padding: 0;\n  font-size: 20px;\n  color: gray;\n}\n\n.restaurant-detail__foods li, .restaurant-detail__drinks li {\n  margin-bottom: 10px;\n}\n\n/* Review Styles */\n.detail-review-item {\n  margin-bottom: 20px;\n}\n\n.header-review {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 10px;\n}\n\n.name-review {\n  display: flex;\n  align-items: center;\n}\n\n.name-review i {\n  margin-right: 10px;\n}\n\n.card {\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  margin-bottom: 20px;\n  padding: 15px;\n}\n\n.header-review {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 10px;\n}\n\n.name-review {\n  display: flex;\n  align-items: center;\n}\n\n.name-review i {\n  margin-right: 10px;\n}\n\n.body-review {\n  line-height: 1.5;\n}\n\n\n.restaurant-detail__menu-list {\n  display: flex;\n  gap: 20px;\n}\n\n.foods,\n.drinks {\n  flex: 1;\n}\n\n.restaurant-detail__foods,\n.restaurant-detail__drinks {\n  list-style: none;\n  padding: 0;\n}\n\n.restaurant-detail__foods li,\n.restaurant-detail__drinks li {\n  margin-bottom: 10px;\n  display: flex;\n  align-items: center;\n}\n\n.restaurant-detail__foods li i,\n.restaurant-detail__drinks li i {\n  margin-right: 10px;\n  font-size: 1.2em;\n}\n\n.like{\n  cursor: pointer;\n  background: salmon;\n  width: min-content;\n  padding: 10px;\n  border-radius: 100%;\n  color: white;\n  font-size: 50px;\n  position: fixed;\n  bottom: 100px;\n  right: 10px;\n}\n\n.like > button{\n  border: none;\n}\n\nh2{\n  margin: 50px 0px;\n}\n\n\n.resto-item_image-container{\n  width: 100%;\n}\n"],"sourceRoot":""}]);
